import React from "react";
import { Link } from "react-router-dom";
class HeaderComponent extends React.Component
{
    render()
    {
        return (
            <div>
                <nav>
                    <ul>
                        <li><Link to="/Message" className="btn btn-link">Test</Link></li>
                    </ul>
                </nav>
            </div> 
        )
    }
}

export default HeaderComponent;